import { ImgPers } from "../../common/Img";

import {
  ContentContainer,
  BlockContainer,
  Title,
  Wave,
  RoadmapContainer,
  RoadmapImage,
  RoadmapBlock,
  RoadmapImage1,
  RoadmapImage2,
  RoadmapImage3,
  RoadmapImage4,
  RoadmapImage5,
} from "./styles";

const Roadmap = () => {

  return (
    <BlockContainer>

      <ContentContainer>

        <RoadmapContainer>

          <RoadmapBlock>
            <Title>Roadmap</Title>
          </RoadmapBlock>

          <RoadmapBlock>
            <RoadmapImage1>
              <ImgPers src="Roadmap1.jpg" width="100%" height="100%" />
            </RoadmapImage1>
          </RoadmapBlock>

          <RoadmapBlock>
            <RoadmapImage2>
              <ImgPers src="Roadmap2.jpg" width="100%" height="100%" />
            </RoadmapImage2>
          </RoadmapBlock>

          <RoadmapBlock>
            <RoadmapImage3>
              <ImgPers src="Roadmap3.jpg" width="100%" height="100%" />
            </RoadmapImage3>
          </RoadmapBlock>

          <RoadmapBlock>
            <RoadmapImage4>
              <ImgPers src="Roadmap4.jpg" width="100%" height="100%" />
            </RoadmapImage4>
          </RoadmapBlock>

          <RoadmapBlock>
            <RoadmapImage5>
              <ImgPers src="Roadmap5.jpg" width="100%" height="100%" />
            </RoadmapImage5>
          </RoadmapBlock>

        </RoadmapContainer>

      </ContentContainer>

      <Wave>
        <svg viewBox="0 0 500 80" preserveAspectRatio="none" style={{height: "100%", width: "100%", bottom: "0", left: "0", display: "block" }} >
          <path d="M0.00,49.98 C150.00,150.00 349.20,-50.00 500.00,49.98 L500.00,150.00 L0.00,190.00 Z" style={{stroke: "none", fill: "#FCFCA4", bottom: "0"}} />
        </svg>
      </Wave>

    </BlockContainer>
  );
};

export default (Roadmap);
