import styled from "styled-components";

export const BlockContainer = styled("section")`
  position: relative;
  width: 100%;
  height: 100%;

  margin-right: auto;
  margin-left: auto;
  padding: 0;

  background: #111;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
`;

export const ContentContainer = styled("section")`
  width: 100%;
  height: 100%;
  padding: 0 0 20% 0;
  margin: 0;

  @media only screen and (max-width: 992px) {
    padding: 0 0 20% 0;
  }
  @media only screen and (max-width: 768px) {
    padding: 0 0 5% 0;
  }
  @media only screen and (max-width: 576px) {
    padding: 0 0 0% 0;
  }
`;

export const Title = styled("section")`
  color: #FFF;
  font-family: BalooBold;
  font-size: 100px;
  text-shadow: 0 4px 4px rgba(81, 21, 0, 25%);

  @media only screen and (max-width: 768px) {
    font-size: 85px;
  }
  @media only screen and (max-width: 576px) {
    font-size: 60px;
  }
`;

export const Wave = styled("section")`
  position: absolute;
  width: 100%;
  height: 100px;
  bottom: -1px;
  left: 0;
  z-index: 4;

  @media only screen and (max-width: 992px) {
    height: 80px;
  }
  @media only screen and (max-width: 576px) {
    height: 50px;
  }
`;

export const RoadmapContainer = styled("section")`
  padding: 0;
  
  @media only screen and (max-width: 768px) {
    padding: 0 0 5rem 0;
  }
`;

export const RoadmapImage = styled("section")`
  height: 95%;
  aspect-ratio: 1;

  box-shadow: 2px 2px 10px rgba(0, 0, 0, 80%);

  @media only screen and (max-width: 992px) {
    height: 90%;
  }
  @media only screen and (max-width: 768px) {
    height: 75%;
  }
  @media only screen and (max-width: 576px) {
    width: 100%;
    height: auto;
  }
`;

export const RoadmapBlock = styled("section")`
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100vh;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
`;

export const RoadmapImage1 = styled(RoadmapImage)`
  transform: rotate(5deg) translate(-5%, 0%);

  @media only screen and (max-width: 576px) {
    transform: rotate(0deg) translate(0, -16%);
  }
`;

export const RoadmapImage2 = styled(RoadmapImage)`
  transform: rotate(-3deg) translate(5%, -2%);

  @media only screen and (max-width: 576px) {
    transform: rotate(0deg) translate(0, -12%);
  }
`;

export const RoadmapImage3 = styled(RoadmapImage)`
  transform: rotate(2deg) translate(2%, -2%);

  @media only screen and (max-width: 576px) {
    transform: rotate(0deg) translate(0, -8%);
  }
`;

export const RoadmapImage4 = styled(RoadmapImage)`
  transform: rotate(-3deg) translate(-2%, 3%);

  @media only screen and (max-width: 576px) {
    transform: rotate(0deg) translate(0, -4%);
  }
`;

export const RoadmapImage5 = styled(RoadmapImage)`
  transform: rotate(2deg) translateY(4%);

  @media only screen and (max-width: 576px) {
    transform: rotate(0deg) translate(0, 0);
  }
`;